.zoom-controls-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	width: 250px;
	height: 20px;
	bottom: 55px;
	margin-left: 20px;
	margin-bottom: 20px;
	position: fixed;
	opacity: 1;

	-webkit-transition: opacity 1s;
	-o-transition: opacity 1s;
	transition: opacity 1s;
}

.zoom-controls-hide {
	opacity: 0;
}

.zoom-input-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;

	width: 200px;
	height: 20px;

	margin-left: 5px;

	border-left: 2px solid white;
	border-right: 2px solid white;
}

.zoom-slider {
	-webkit-appearance: none;

	width: 200px;
	height: 2px;
	background-color: white;
	cursor: pointer;
}

.zoom-value {
	margin-left: 10px;
	color: white;
}

input[type=range]::-webkit-slider-thumb {
	-webkit-appearance: none;
	border: 3px solid #02fcbd;
	width: 20px;
	height: 15px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}


