.game-view {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	overflow: auto;
	font-family: 'Orbitron', sans-serif;
}

.game-view::-webkit-scrollbar { 
    display: none; 
}

.game-view-overlay {
	position: absolute;
	width: 1920px;
	height: 1280px;
	background-color: black;
	z-index: 1;

	-webkit-transition: opacity 6s;
	-o-transition: opacity 6s;
	transition: opacity 6s;

	-webkit-transition-delay: 2.5s;
	-o-transition-delay: 2.5s;
	   transition-delay: 2.5s;
}

.hide {
	opacity: 0;
	pointer-events: none;
}

.layer-1 {
	position: absolute;
	width: 1920px;
	height: 1280px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.menu-icon {
	margin-top: 10px;
	position: fixed;
	width: 50px;
	cursor: pointer;
	opacity: 1;

	-webkit-transition: opacity 1s;
	-o-transition: opacity 1s;
	transition: opacity 1s;
}

.menu-icon-hide {
	opacity: 0;
}

.background-image {
	position: absolute;
}

.tablet {
	position: absolute;
	width: 100px;
	top: 600px;
	left: 900px;
}

.lens1 {
	position: absolute;
	width: 90px;
	top: 200px;
	left: 200px;

	-webkit-animation: 1.5s linear pulse;

	        animation: 1.5s linear pulse;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

.lens2 {
	position: absolute;
	width: 90px;
	top: 200px;
	left: 1700px;

	-webkit-animation: 1.5s linear pulse;

	        animation: 1.5s linear pulse;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

.lens3 {
	position: absolute;
	width: 90px;
	top: 900px;
	left: 600px;

	-webkit-animation: 1.5s linear pulse;

	        animation: 1.5s linear pulse;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}


@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}


@keyframes pulse {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

.interact {
	cursor: pointer;
}

.hidden {
	visibility: hidden;
}



